export const getTimeInfo = str => {
  if (!str) return ''
  const date = new Date(str)
  const time = new Date().getTime() - date.getTime() // - =  = 
  if (time < 0) return ''
  if (time / 1000 < 60) return ''
  if (time / 60000 < 60) return parseInt(time / 60000, 10) + ''
  if (time / 3600000 < 24) return parseInt(time / 3600000, 10) + ''
  if (time / 86400000 < 31) return parseInt(time / 86400000, 10) + ''
  if (time / 2592000000 < 12) return parseInt(time / 2592000000, 10) + ''
  return parseInt(time / 31536000000, 10) + ''
}



