import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Switch, Route, Redirect } from 'react-router-dom'
import Loadable from 'react-loadable'
import Loading from '@components/loading'

// 
import List from '@pages/list'
// Loadable
const LoadWrapper = importComponent => {
  return Loadable({
    loader: importComponent,
    loading: Loading
  })
}
// 
const Login = LoadWrapper(() => import(/* webpackChunkName: "login" */'@pages/login'))
const NoMatch = () => <Redirect to="/all" />
// 
export const routes = [
  { path: '/', exact: true, component: NoMatch },
  { path: '/login', component: Login, title: '' },
  { path: '/all', component: List, title: '' },
  { path: '/good', component: List, title: '' },
  { path: '/share', component: List, title: '' },
  { path: '/ask', component: List, title: '' },
  { path: '/job', component: List, title: '' },
  { component: NoMatch }
]
// routespropsroutes
export class RouteViews extends Component {
  static propTypes = {
    routes: PropTypes.array
  }

  render () {
    const { routes } = this.props
    return (
      <Switch>
        {routes.map(route => {
          return route.path
            ? <Route
              key={route.path}
              path={route.path}
              exact={route.exact}
              render={props =>
                <route.component
                  {...props}
                  routes={route.routes}
                />
              }
            />
            : <Route
              key="404"
              render={props =>
                <route.component
                  {...props}
                  routes={route.routes}
                />
              }
            />
        })}
      </Switch>
    )
  }
}



